import React from 'react';
import { FormControl, FormLabel, RadioGroup, FormControlLabel, Radio } from '@mui/material';

const RadioButtonGroup = ({ value, onChange,title, options,name }) => (
  <FormControl sx={{mt:1}}>
    <FormLabel id="demo-radio-buttons-group-label">{title || "no title"}</FormLabel>
    <RadioGroup
    sx={{flexDirection:'row'}}
      aria-labelledby="demo-radio-buttons-group-label"
      value={value}
      onChange={onChange}
      name={name}
    >
    {options.map((item,index)=><FormControlLabel key={index} value={item.value} control={<Radio />} label={item.label} />)}
    </RadioGroup>
  </FormControl>
);

export default RadioButtonGroup;
